body {
	line-height: 1.6em;
}
a {
	text-decoration: none;
	color: #369;
}
.text-input {
	color: #7F7F7F;
	border-radius: 4px;
	width: 255px;
	height: 32px;
	line-height: 22px;
	background-color: #FBFBFB;
	border: 1px solid #C1C1C1;
	padding: 0px 2px;
	margin-bottom: 15px;
	padding: 0 1.5%;
}
.autocomplete {
	border: 1px solid #999;
	background: white;
	cursor: default;
	text-align: left;
	max-height: 350px;
	overflow: auto;
	margin: 0px 6px 6px 0px;
	cursor: default;
	text-align: left;
	strong {
		font-weight: normal;
		color: #39F;
	}
	div {
		padding: 5px 10px;
		white-space: nowrap;
		overflow: hidden;
	}
	.selected {
		background: #C9E6ED;
	}
}
Ul.tree {
	list-style-type: none;
	background: url(../../static/image/vline.png) repeat-y;
	margin: 0;
	padding: 0;
}
ul.tree {
	ul {
		list-style-type: none;
		background: url(../../static/image/vline.png) repeat-y;
		margin: 0;
		padding: 0;
		margin-left: 20px;
	}
	li {
		margin: 0;
		padding: 0 12px;
		line-height: 20px;
		background: url(../../static/image/node.png) no-repeat;
		font-weight: bold;
		&:last-child {
			background: #fff url(../../static/image/lastnode.png) no-repeat;
		}
	}
}
#hierarchy {
	width: 49%;
	float: left;
}
#details {
	width: 49%;
	float: left;
	h2 {
		margin-bottom: 5px;
	}
	ul {
		li {
			margin-left: 20px;
		}
	}
}
.definition {
	padding: 10px;
	background-color: #eee;
	margin-bottom: 15px;
	-webkit-border-radius: 11px;
	-moz-border-radius: 11px;
	border-radius: 11px;
}
.qlf_box {
	clear: both;
	display: block;
	margin-bottom: 5px;
	ul {
		margin: 0px;
		list-style: none;
		padding: 0px;
		li {
			width: 45%;
			margin-left: 5%;
			float: left;
		}
	}
}
.search_submit {
	clear: both;
	display: block;
	margin: 10px 0 0 15px;
}
.spacer {
	clear: both;
	display: block;
}
.btn-custom {
	background-color: hsl(195, 60%, 35%) !important;
	background-repeat: repeat-x;
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#2d95b7", endColorstr="#23748e");
	background-image: -khtml-gradient(linear, left top, left bottom, from(#2d95b7), to(#23748e));
	background-image: -moz-linear-gradient(top, #2d95b7, #23748e);
	background-image: -ms-linear-gradient(top, #2d95b7, #23748e);
	background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #2d95b7), color-stop(100%, #23748e));
	background-image: -webkit-linear-gradient(top, #2d95b7, #23748e);
	background-image: -o-linear-gradient(top, #2d95b7, #23748e);
	background-image: linear-gradient(#2d95b7, #23748e);
	border-color: #23748e #23748e hsl(195, 60%, 32.5%);
	color: #fff !important;
	text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.16);
	-webkit-font-smoothing: antialiased;
	margin-bottom: 10px;
}
.btn-large {
	font-size: 130%;
	line-height: normal;
	padding: 9px 14px 9px;
	-webkit-border-radius: 6px;
	-moz-border-radius: 6px;
	border-radius: 6px;
}
